import common from '../../../common/common.js'

export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			name:'aa',
			shopInfo:[],		//用户信息
			hotline:'0573-82082008',
			maishou:'',
			todayOrderNums:0,
			orderNums:0,
			shenqingView:0,
		}
	},
	onLoad() {
		var that = this
		var shopInfo = uni.getStorageSync('shopInfo');
		
		if(shopInfo == '' || shopInfo == null || typeof(shopInfo) == undefined){
			
		}else{
			that.shopInfo = shopInfo;
			that.getShopInfo();
				
			that.getMerchantContact();
			that.getMerchantShenqingView();
		}
		
		
	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		console.log('下拉刷新中...');
		var shopInfo = uni.getStorageSync('shopInfo');
		if(shopInfo == '' || shopInfo == null || typeof(shopInfo) == undefined){
			
		}else{
			that.shopInfo = shopInfo;
			that.getShopInfo();
			that.getMerchantContact();
			that.getMerchantShenqingView();
		}
		
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	methods: {
		
		//店铺打烊
		changeShopShundown(e){
			let that = this
			let attr = e.currentTarget.dataset.attr
			let shutdown = that.userInfo.shop_shundown
			var userInfo = uni.getStorageSync('userInfo');
			console.log(shutdown);
			console.log(attr);
			if(attr == shutdown){
				return false
			}else{
				uni.request({
					url: common.sjApiUrl + 'merchant/changeShopShundown',
					method: 'GET',
					data: {
						shopid:userInfo.shop_id,
						shutdown:attr
					},
					success: res => {
						// let data = res.data
						// let userInfo = data.userInfo
						// uni.setStorageSync('userInfo',userInfo);
						// that.userInfo = userInfo
						that.getUserInfo();
					},
					fail: () => {},
					complete: () => {}
				});
			}
		},
		//获取商家订单统计信息
		getMerchantOrderInfo(){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			var shopid = userInfo.shop_id
			uni.request({
				url: common.sjApiUrl + 'merchant/getMerchantOrderInfo',
				method: 'GET',
				data: {
					shopid:shopid
				},
				success: res => {
					that.todayOrderNums = res.data.todayOrderNums
					that.orderNums = res.data.orderNums
				},
				fail: () => {},
				complete: () => {}
			});
		},
		////获取店铺申请查看价格数量
		getMerchantShenqingView(){
			var that = this
			var shopInfo = uni.getStorageSync('shopInfo');
			var shopid = shopInfo.shop_id
			uni.request({
				url: common.sjApiUrl + 'merchant/getMerchantShenqingView',
				method: 'GET',
				data: {
					shopid:shopid
				},
				success: res => {
					that.shenqingView = res.data.nums
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//获取联系信息
		getMerchantContact(){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			var marketid = userInfo.shop_marketid
			uni.request({
				url: common.sjApiUrl + 'merchant/getMerchantContact',
				method: 'GET',
				data: {
					marketid:marketid
				},
				success: res => {
					console.log(res)
					that.hotline = res.data.hotline
					that.maishou = res.data.maishou
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//核销
		hexiaoOrder(){
			var that = this
			if(that.shopInfo != ''){
				uni.navigateTo({
					url: '/pages/shop/index/hexiao',
				});
			}else{
				that.checkLogin();
			}
		},
		//资金提现
		tixian(){
			var that = this
			if(that.shopInfo != ''){
				uni.navigateTo({
					url: '/pages/shop/index/recharge',
				});
			}else{
				that.checkLogin();
			}
		},
		//商品列表
		goodsList(){
			var that = this
			if(that.shopInfo != ''){
				uni.navigateTo({
					url: '/pages/shop/goods/goods',
				});
			}else{
				that.checkLogin();
			}
			
		},
		//商品类目
		goodsCat(){
			var that = this				
			if(that.shopInfo != ''){
				uni.navigateTo({
					url: '/pages/shop/goods/cat',
				});
			}else{
				that.checkLogin();
			}
		},
		//订单列表
		orderList(){
			var that = this
			if(that.shopInfo != ''){
				uni.navigateTo({
					url: '/pages/shop/index/orderlist'
				});
			}else{
				that.checkLogin();
			}
		},
		//申请列表
		shengqingList(){
			var that = this
			if(that.shopInfo != ''){
				uni.navigateTo({
					url: '/pages/shop/index/shenqing'
				});
			}else{
				that.checkLogin();
			}
		},
		//商品订单列表
		goodsOrderList(){
			var that = this
			if(that.shopInfo != ''){
				uni.navigateTo({
					url: '/pages/shop/orders/orders',
				});
			}else{
				that.checkLogin();
			}
		},
		//特推订单列表
		superOrderList(){
			var that = this
			if(that.shopInfo != ''){
				uni.navigateTo({
					url: '/pages/shop/superorders/superorders',
				});
			}else{
				that.checkLogin();
			}
		},
		//绑定银行卡
		bindCard(){
			var that = this
			if(that.shopInfo != ''){
				uni.navigateTo({
					url: '/pages/shop/index/bindcard',
				});
			}else{
				that.checkLogin();
			}
		},
		//客服热线
		contactKefu(){
			var that = this
			var hotline = that.hotline
			uni.makePhoneCall({
				phoneNumber:hotline
			})
		},
		//返回首页
		backHome(){
			var that = this
			uni.reLaunch({
				url:'/pages/index/index'
			})
		},
		//联系买手
		userInfoSetting(){
			var that = this
			if(that.shopInfo != ''){
				uni.navigateTo({
					url: '/pages/shop/user/user',
				});
			}else{
				that.checkLogin();
			}
		},
		
		//登录
		userlogin(){
			uni.navigateTo({
				url: '/pages/shop/userlogin/userlogin',
			});
		},
		
		checkLogin(){
			var that = this				
			var shopInfo = uni.getStorageSync('shopInfo');	
			console.log('shopInfo',shopInfo)
			if(shopInfo == ''){
				
				uni.showModal({
					title:'提示',
					content:'您还没有登录,现在去登录吗？',
					success: res => {
						if(res.confirm){					
							uni.navigateTo({
								url: '/pages/shop/userlogin/userlogin',
								success: res => {
									
								},
								fail: () => {},
								complete: () => {}
							});
						}else{
							return false;
						}
					},
					fail: () => {},
					complete: () => {}
				});
				
			}
		},
		
		//退出登录
		loginout(){
			var that = this
			uni.showModal({
				title:'提示',
				content:'确定要退出吗？',
				success: res => {
					if(res.confirm){
						uni.setStorageSync('shopInfo','');
						uni.redirectTo({
							url: '/pages/shop/userlogin/userlogin',
							success: res => {
								console.log('退出')
							},
							fail: () => {},
							complete: () => {}
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//
		jumpUrl(e){
			var that = this
			var url = e.currentTarget.dataset.url
			uni.navigateTo({
				url: url
			});
		},
		
		//更新获取店铺信息
		getShopInfo(){
			//console.log('获取店铺信息');
			var that = this
			var shopInfo = uni.getStorageSync('shopInfo');
			uni.request({
				url: common.sjApiUrl + 'merchant/getUserInfo',
				method: 'GET',
				data: {
					shop_id:shopInfo.shop_id
				},
				success: res => {
					var shop = res.data.userInfo
					if(shop != ''){
						that.shopInfo = shop
						uni.setStorageSync('shopInfo',shop);
						if(shop.shop_status == 0){
							uni.navigateTo({
								url: '/pages/shop/user/order',
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						}
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
	}
}
